html {
  scroll-behavior: smooth;
}

section {
  margin: 4rem 0;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1s ease-out, transform 1s ease-out;
}

/* When visible */
section.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Card grid for projects */
.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1rem;
}
